@use '../abstracts/variables' as abs;

 .btn{
    &:link,
    &:visited{
        text-transform: uppercase;
        text-decoration: none;
        padding: 1.5rem 4rem;
        display: inline-block;
        border-radius: 1rem;
        transition: all 0.2s;
        position: relative;
        
    }
   
    &:hover{
        transform: translateY(-0.3rem);
        box-shadow: 0 1rem 2rem rgba(abs.$color-black, 0.2);

        &::after{
            transform: scaleX(1.4) scaleY(1.6);
            opacity: 0;
        }
    }
   
    &:active{
       transform: translateY(-1px);
       box-shadow: 0 0.5rem 1rem rgba(abs.$color-black, 0.2);
   }
   
    &-white{
        background-color: abs.$color-white;
        color: abs.$color-dark-gray;
        font-weight: 700;
        animation: moveBtnUp 0.5s ease-out .75s;
        animation-fill-mode: backwards;

        &::after{
            background-color: abs.$color-white;
        }
    }
   
    &::after{
        content: "";
        display: inline-block;
        height: 100%;
        width: 100%;
        border-radius: 1rem;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        transition: all 0.8s;
    }
   
  
   
  
}
 